.wp-block-lf-icon-list ul {
  column-count: var(--column-count); }
  @media (max-width: 416px) {
    .wp-block-lf-icon-list ul {
      column-count: 1; } }
  .wp-block-lf-icon-list ul li {
    margin-bottom: var(--list-gap); }

.wp-block-lf-icon-list[class*="is-style-list-"] ul {
  margin: 0 0 1.5em !important;
  padding: 0 !important;
  list-style: none; }
  .wp-block-lf-icon-list[class*="is-style-list-"] ul li {
    list-style: none;
    text-indent: calc(var(--icon-size)*-1 - 1em);
    padding-left: calc(var(--icon-size) + 1em); }
    .wp-block-lf-icon-list[class*="is-style-list-"] ul li:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: var(--icon-size);
      width: var(--icon-size);
      margin-right: 1rem;
      background-size: cover;
      background-position: 50%;
      transform: translateY(-10%); }

